import { createApp } from 'vue'
import { createStore } from 'vuex'
import App from './App.vue'
import router from './router'


const store = createStore({
    state () {
      return {
        visited: false
      }
    },
    mutations: {
      visit (state) {
        state.visited = true
      }
    },

    getters : {
        visited(state){
            return state.visited
        }
    }
  })

createApp(App).use(store).use(router).mount('#app')
