<template>
    <h4 style="--color : white;" data-text="BIENVENUE" id="enter-title">BIENVENUE</h4>
    <div class="animated-top">
        <span class="meteor" id="span1"></span>
        <span class="meteor" id="span2"></span>
        <span class="meteor" id="span3"></span>
        <span class="meteor" id="span4"></span>
        <span class="meteor" id="span5"></span>
        <span class="meteor" id="span6"></span>
        <span class="meteor" id="span7"></span>
        <span class="meteor" id="span8"></span>
        <span class="meteor" id="span9"></span>
        <span class="meteor" id="span10"></span>
        <span class="meteor" id="span11"></span>
        <div id="stars"></div>
        <div id="stars2"></div>

    <img id ="superman" src="@/assets/superman.png" />
</div>
</template>

<script>
export default {
    name : 'BackgroundComp'
    ,

    mounted(){

        let visited=  this.$store.getters.visited
        let superman = document.querySelector("#superman")
        if(!visited){
            this.PingMessage("BIENVENUE", 'white')
            let elementsToAnimateAppear = document.querySelectorAll("nav, .content")
            elementsToAnimateAppear.forEach(e => {e.classList.add("animateOpacityApear")})
            superman.classList.add('first-fly')

        }else{
            superman.classList.add('fly')
        }

        this.$store.commit('visit')
    },

    methods: {
        PingMessage(message, color){
  

            const enter_tilte =  document.querySelector('#enter-title')
            enter_tilte.classList.add('show-message')
            setTimeout(()=>{
                enter_tilte.classList.remove('show-message')
            }, 4500)
            enter_tilte.textContent = message
            enter_tilte.setAttribute('data-text', message);
            enter_tilte.style.setProperty ('--color', color)
        }
    },
}

</script>

