<template>
    <NavBar></NavBar>
    <BackgroundComp></BackgroundComp>
    <main class="content">
        <h1>PROJETS</h1>
        <div class="project-container">
            <ProjectComp @closeImages="showArrows()" @images="hideArrows()" v-for="(project, key) in projects" :key="key" :currentProject="key == currentProject"
                :content="project"></ProjectComp>
            <button @click="right()" id="right" :class="{ activated: currentProject < this.projets_count - 1 }"
                class="arrow">
                <img src="@/assets/arrow.png" />
            </button>
            <button @click="left()" id="left" class="arrow" :class="{ activated: currentProject > 0 }">
                <img src="@/assets/arrow.png" />
            </button>
        </div>
    </main>
</template>

<script>
import NavBar from "@/components/NavBar.vue";
import BackgroundComp from "@/components/BackgroundComp.vue";
import ProjectComp from "@/components/ProjectComp.vue";

export default {
    name: "ProjectsPage",
    components: {
        NavBar,
        BackgroundComp,
        ProjectComp,
    },
    data() {
        return {
            currentProject: 0,
            projects: [
                {
                    title: "EDUCAT",
                    description:
                        "SITE WEB DE JEUX EDUCATIFS. DEVELOPPEMENT FRONT & BACK.",
                    technologies: ['vue',"java", "html", "css", "js"],
                    images: ["educat_1", "educat_3", "educat_4", "educat_5","educat_2" ],
                },
  
                {
                    title: "RS-COURSES",
                    description:
                        "SITE WEB POUR UN PROFESSIONNEL. DEVELOPPEMENT FRONT & BACK.",
                    technologies: ["vue", "php", "html", "css", "js"],
                    images: ["rs_1", "rs_2", "rs_3"],
                    url : ['https://rs-courses.fr/']
                },

                {
                    title: "IA NUM",
                    description:
                        "IA CAPABLE DE RECONNAITRE DES CHIFFRES. BASEE SUR LES DONNEES MNIST. DEVELOPPEMENT EN EQUIPE",
                    technologies: ["python"],
                    images: ["IA_1", "IA_2"],
                    url : ['https://github.com/HachimiBouizegarene/IA_num']
                },

            ],
            projets_count: 0,
        };
    },
    methods: {
        right() {
            if (this.currentProject + 1 < this.projets_count)
                this.currentProject += 1;
            console.log(this.currentProject);
        },
        left() {
            if (this.currentProject - 1 >= 0) this.currentProject -= 1;
        },
        hideArrows(){
            document.querySelectorAll('.arrow').forEach((e)=>{
                e.classList.add('hidden');
            })
        },
        showArrows(){
            document.querySelectorAll('.arrow').forEach((e)=>{
                e.classList.remove('hidden');
            })
        }
    },

    mounted() {
        this.projets_count = this.projects.length;
    },
};
</script>

