<template>
  <BackgroundComp></BackgroundComp>
  <NavBar></NavBar>
  <div class="content">

    <form @submit.prevent="submitForm">
      <h1>ME CONTACTER</h1>

      <p id="text-mail">MAIL : mlazzouli.pro@gmail.com</p>
      <section>
        <label for="type">STATUT :</label>
        <select id="type" v-model="typeDeContact" name="type" @change="selectOnChange">
          <option value="particulier">PARTICULIER</option>
          <option value="entreprise" selected>ENTREPRISE</option>
        </select>
      </section>

      <!-- Champs pour un particulier -->
      <div v-if="typeDeContact === 'particulier'">
        <section class="semi">
          <label for="name">NOM :</label>
          <input  @input="checkButtonValid" @focusout="checkInput" v-model="name_form" type="text" id="name" />
        </section>
        <section class="semi">
          <label for="forname">PRENOM :</label>
          <input @input="checkButtonValid" @focusout="checkInput" v-model="forename_form" type="text" id="forname" />
        </section>
      </div>
      <section v-if="typeDeContact === 'entreprise'">
        <label for="society">ENTREPRISE :</label>
        <input @input="checkButtonValid" @focusout="checkInput" v-model="society_form" type="text" id="name" />
      </section>

      <section>
        <label for="mail">MAIL :</label>
        <input @input="checkButtonValid" @focusout="checkInput" v-model="mail_form" type="text" id="mail" />
      </section>

      <section>
        <label for="message">MESSAGE :</label>
        <textarea @input="checkButtonValid" @focusout="checkInput" v-model="message_form" id="message"></textarea>
      </section>
      <section>
        <!-- <span id="ping">UNE ERREUR EST SURVENUE</span> -->
        <messageContainer ref="messageContainer"></messageContainer>
        <button class="" id="submit-btn" type="submit">ENVOYER</button>
      </section>
    </form>
  </div>
</template>

<script>
import messageContainer from "@/components/messageContainer.vue"
import NavBar from "@/components/NavBar.vue";
import BackgroundComp from "@/components/BackgroundComp.vue";
export default {
  name: "ContactPage",
  components: {
    NavBar,
    BackgroundComp,
    messageContainer
  },
  data() {
    return {
      typeDeContact: "particulier",
      society_form: '',
      message_form: '',
      forename_form: '',
      name_form: '',
      mail_form: '',
    };
  },

  mounted() {
  },

  methods: {
    checkButtonValid(){
      console.log('cac');
      const emailRegex = /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/;
      document.querySelector('#submit-btn').classList.remove("clickable")

      if(this.message_form.trim()== '' 
      || this.mail_form.trim() == '' || emailRegex.test(this.mail_form) ==false){
        return;
      }
      if(this.typeDeContact== 'particulier' &&
      (this.forename_form.trim() == '' || this.name_form.trim() == '')){
        return;
      }
      if(this.typeDeContact== 'entreprise' && this.society_form.trim() == '' ){
        return;
      }

      document.querySelector('#submit-btn').classList.add("clickable")
    },
    checkInput(e){
      
      e.target.classList.remove('green')
      if(e.target.value.trim() == ""){
        e.target.classList.add('red')
        return;
      }
      const emailRegex = /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/;
      if(e.target.id == 'mail') {
        if(emailRegex.test(e.target.value) ==false){
          e.target.classList.add('red')
          return;
        }

      }
      e.target.classList.remove('red')
      e.target.classList.add('green')

    
    },

    selectOnChange() {
      this.society_form = ''
      this.name_form = ''
      this.forename_form = ''
    },
    async submitForm() {
      let data = {
        'message_form':  this.message_form,
        'mail_form':  this.mail_form,
      }
      if(this.typeDeContact == 'particulier'){
        
        data.name_form = "Particulier : "+  this.name_form + " " + this.forename_form
      }else{
        data.name_form = "Entreprise : " + this.society_form
      }
      let json;

      try{
        const response = await fetch("/server/", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body : JSON.stringify(data)
      })
      json = await response.json()
      if(json.error == true){
        this.$refs.messageContainer.message({'error' : 'ERREUR SERVEUR'})
      }else{
        this.$refs.messageContainer.message({'success' : 'MESSAGE ENVOYE'})
      }
      }catch{
        this.$refs.messageContainer.message({'error' : 'ERREUR SERVEUR'})
      }
    }

  },
};
</script>

