<template>
    <div ref="container" :class="{ 'project': true }">
        <section ref="content" class="active">
            <p>{{ content.description }}</p>
            <h3>{{ content.title }}</h3>
            <div class="technologies">
                <img v-for="(name, key) in content.technologies" :key=key
                    :src="require('@/assets/technologies/' + name + '.png')">
            </div>
            <div class="btns">
                <a target=”_blank” class="dispo" v-if="content.url !== undefined" :href="content.url">CONSULTER</a>
                <a  v-if="content.url === undefined" :href="content.url">EN DEVELOPPEMENT</a>
                <button @click="showImages" id="eye" href="#"><img src="@/assets/eye_icon.png"></button>
            </div>
        </section>
        <img id="background-image" :src="require('@/assets/projects/' + content.images[0] + '.webp')">
        <div ref="images" class="images-container">
            <img v-for="(image, key) in content.images " :key=key class="hidden" :class="{ 'active': key == currentImage }"
                :src="require('@/assets/projects/' + image + '.webp')">
            <div class="image-btns">
                <button id="left" class="arrow" @click="left()"><img src="@/assets/arrow.png" /></button>
                <button id="close" @click="closeImages()">FERMER</button>
                <button id="right" class="arrow" @click="right()"><img src="@/assets/arrow.png" /></button>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "ProjectComp",
    props: {
        currentProject: Boolean,
        content: Object
    },

    data() {
        return {
            last_current: false,
            currentImage: 0,
            images_count: 0
        }
    },
    mounted() {
        this.last_current = this.$props.currentProject
        if (this.$props.currentProject) {
            this.$refs.container.classList.add("active")
        } else {
            this.$refs.container.classList.remove("active")
        }

        this.images_count = this.$props.content.images.length
    },
    methods: {
        showImages() {
            this.$emit('images')
            this.$refs.content.classList.remove("active")
            this.$refs.images.classList.add("active")
        },
        right() {
            if (this.currentImage + 1 < this.images_count)
                this.currentImage += 1;
        },
        left() {
            if (this.currentImage - 1 >= 0) this.currentImage -= 1;
        },
        closeImages() {
            this.$emit('CloseImages')
            this.$refs.content.classList.add("active")
            this.$refs.images.classList.remove("active")
        }


    },

    updated() {
        if (this.last_current != this.$props.currentProject) {
            this.last_current = this.$props.currentProject
            this.$refs.container.classList.add("swap")
            setTimeout(() => {
                this.$refs.container.classList.remove("swap")
            }, 800)
            if (this.$props.currentProject) {
                setTimeout(() => {
                    this.$refs.container.classList.add("active")
                }, 400)

            } else {
                setTimeout(() => {
                    this.$refs.container.classList.remove("active")
                }, 400)
            }
        }

    },
}
</script>


