<template>
  <div class="competence_content">
    <h4>{{ title }}</h4>
    <div>
      <p ref="text_container">>_</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "CompetenceComp",
  props: {
    title: String,
    list: Array,
  },

  methods: {
    writeLetters(element, words, index) {
      let word = words[index];

      index = (index + 1) % words.length;

      const interval = setInterval(() => {
        if (word === "") {
          clearInterval(interval);
          this.blink(element);
          const blinker = setInterval(() => {
            this.blink(element);
          }, 800);
          setTimeout(() => {
            clearInterval(blinker);
            this.eraseLetters(element, words, index);
          }, 1200);
        } else {
          const char = word.slice(0, 1);
          word = word.substr(1);
          this.writeChar(element, char);
        }
      }, 150);
    },

    eraseLetters(element, words, index) {
      const interval = setInterval(() => {
        if (element.textContent === ">_") {
          clearInterval(interval);
          this.blink(element);
          const blinker = setInterval(() => {
            this.blink(element);
          }, 800);
          setTimeout(() => {
            clearInterval(blinker);
            element.innerHTML = ">_";
            this.writeLetters(element, words, index);
          }, 1200);
        } else {
          this.eraseChar(element);
        }
      }, 150);
    },

    eraseChar(element) {
      let text =
        element.textContent.slice(0, -2) + element.textContent.slice(-1);
      element.innerHTML = text;
    },

    writeChar(element, char) {
      let elementSliced = element.textContent.slice(0, -1);
      let text = elementSliced + char + "_";
      element.innerHTML = text;
    },

    blink(element) {
      let char = element.textContent.charAt(element.textContent.length - 1);
      element.innerHTML = element.textContent.slice(0, -1) + "&nbsp";
      setTimeout(() => {
        element.innerHTML = element.textContent.slice(0, -1) + char;
      }, 150);
    },
  },

  mounted() {
    this.writeLetters(this.$refs.text_container, this.list, 0);
  },
};
</script>

